<template>
	<div class="shopcart-wraper">
		<div class="content" @click='toggleList'>
			<div class="content-left">
				<div class="logo-wraper">
					<p class="logo" :class='count>0?"highlightlogo":""'></p>
					<p class="count" v-show='count'>{{count}}</p>
				</div>
				<div class="price" :class='{whiteColor:totalPrice>0}'>￥{{totalPrice}}</div>
				<div class="desc">另需配送费￥{{deliveryPrice}}元</div>
			</div>
			<div class="content-right" :class='payClass' @click.stop='pay'>
          		{{payDesc}}
 			</div>
 			
		</div>
		<div class="ball-wraper">
			<div v-for='(ball,index) in balls' >
				<transition 
				@before-enter='beforeDrop' 
				@enter='droping' 
				@after-enter='afterDrop'>
					<div class="ball" v-show='ball.show'>
						<div class="inner innerhook"></div>
					</div>
				</transition>
			</div>
		</div>
		<transition name='slide-up'>
			<div class="shop-cart-detail-wraper" v-show='listShow'>
				<shop-cart-detail ref='shopcartdetail' :selectFood='selectFood'></shop-cart-detail>
			</div>
		</transition>
		<transition name='fade'>
			<div class="bg" v-show='listShow' @click.stop='toggleList'></div>
		</transition>
	</div>
</template>
<script type="text/javascript">
	const BALL_LEN = 10;
	function createBall(){
		let balls = [];
		for (var i = 0; i < BALL_LEN; i++) {
			balls.push({"show":false})
		}
		return balls
	}
	import shopCartDetail from 'components/shopcartdetail/shopcartdetail'
	export default{
		props:{
			selectFood:{
				type:Array,
				default(){
					return []
				}
			},
			deliveryPrice:{
				type:Number,
				default:0
			},
			minPrice:{
				type:Number,
				default:0
			}
		},
		data(){
			return {
				balls:createBall(),
				dropBalls:[],
				fold:false
			}
		},
		methods:{
			drop(el){
				for (var i = 0; i < this.balls.length; i++) {
					const ball = this.balls[i];
					if (!ball.show) {
						ball.show = true;
						ball.el = el;
						this.dropBalls.push(ball);
						return
					}
				}
			},
			beforeDrop(el){
				const ball = this.dropBalls[this.dropBalls.length-1];
				const rect = ball.el.getBoundingClientRect();
				const x = rect.left-40;
				const y = -(window.innerHeight-rect.top-18);
				el.style.display = '';
				el.style.transform = el.style.webkitTransform = `translate3d(0,${y}px,0)`;
        		const inner = el.getElementsByClassName('innerhook')[0];
        		inner.style.transform = inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
				console.log(x)
			},
			droping(el,done){
				 this._reflow = document.body.offsetHeight;
		        el.style.transform = el.style.webkitTransform = `translate3d(0,0,0)`;
		        const inner = el.getElementsByClassName('innerhook')[0];
		        inner.style.transform = inner.style.webkitTransform = `translate3d(0,0,0)`;
		        el.addEventListener('transitionend', done)
			},
			afterDrop(el){
				console.log(this.dropBalls)
				const ball = this.dropBalls.shift();

		        if (ball) {
		          ball.show = false
		          el.style.display = 'none';
		        }
			},
			toggleList(){
				if (!this.totalPrice) {
					return;
				}
				this.fold = !this.fold;
				this.$emit('addbg',this.listShow)
			},
			pay(){
				if (this.totalPrice<this.minPrice) {
					return
				}
				alert(`餐费${this.totalPrice}`)
			}
		},
		computed:{
			totalPrice(){
				let total = 0;
				this.selectFood.forEach((food)=>{
					total+=food.count*food.price
				})
				return total
			},
			count(){
				let count = 0;
				this.selectFood.forEach((food)=>{
					count+=food.count
				})
				return count
			},
			payDesc(){
				if (this.totalPrice === 0) {
					return `￥${this.minPrice}元起送`
				}else if (this.totalPrice < this.minPrice) {
					let diff = this.minPrice - this.totalPrice;
					return `还差${diff}元起送`
				}else{
					return '去结算'
				}

			},
			payClass(){
				if (this.totalPrice<this.minPrice) {
					return 'not-enough'
				}else{
					return 'enough'
				}
			},
			listShow(){
				if (!this.totalPrice) {
					this.fold = true;
					return false
				}
				let show = !this.fold
				if (show) {
					this.$nextTick(()=>{
						this.$refs.shopcartdetail.refresh()
					})
				}
				return show;
			}
		},
		components:{
			shopCartDetail
		}
	}
</script>
<style type="text/css" scoped>
	.shopcart-wraper{
		width: 100%;
		height: 48px;
		position: fixed;
		bottom: 0;
		left: 0
	}
	.content{
		display: flex;
		flex-direction: row;
		height: 100%;
		background: #07111b
	}
	.content-left{
		flex: 1;
		display: flex;
		align-items: center;
	}
	.logo-wraper{
		width: 56px;
		height: 56px;
		background: #07111b;
		border-radius: 50%;
		position: relative;
		top: -5px;
		margin: 0 12px;
		padding: 6px;
		box-sizing: border-box;
	}
	.content-right{
		flex:  0 0 105px;
		width: 105px;
		background: #2b333b;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 12px;
		font-weight: 700;
		color: #999

	}
	.logo{
		width: 44px;
		height: 44px;
		background: #ccc;
		border-radius: 50%
	}
	.highlightlogo{
		background: #00a0dc
	}
	.price{
		font-size: 16px;
		padding-right: 12px;
		font-weight: 700;
		border-right: 1px solid hsla(0,0%,100%,.1);
		color: #999
	}
	.desc{
		color: #999;
		font-size: 10px;
		margin-left: 12px
	}
	.count{
		height: 16px;
		line-height: 16px;
		text-align: center;
		padding: 0 5px;
		border-radius: 10px;
		background: linear-gradient(90deg,#fc9153,#f01414);
		position: absolute;
		right: 0;
		top: 0;
		color: #fff;
		font-size: 10px;
		font-weight: 700
	}
	.whiteColor{
		color: #fff
	}
	
	.enough{
		background: #00b43c;
		color: #fff
	}
	.not-enough{
		background:#2b333b 
	}
	.ball{
		
		
		position: fixed;
		left: 32px;
		bottom: 22px;
		z-index: 200;
		transition: all .5s cubic-bezier(0.49, -0.29, 0.75, 0.41)
	}
	.ball .inner{
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: rgba(0,166,220,1);
		background: red;
		transition: all .5s linear
	}
	.shop-cart-detail-wraper{
		width: 100%;
		/*height: 500px;*/
		position: absolute;
		top: 0px;
		left: 0;
		z-index: -1;
		transform: translate3d(0,-100%,0);
		/*max-height: 217px*/
	}
	.slide-up-enter-active{
		transition: all .2s linear
	}
	.slide-up-leave-active{
		transition: all .2s linear
	}
	.slide-up-enter,.slide-up-leave-to{
		transform: translate3d(0,0,0)
	}
	.bg{
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,.6);
		position: fixed;
		top: 0;
		left: 0;
		z-index: -2
	}
	.fade-enter-active{
		transition: all .5s;
	}
	.fade-leave-active{
		transition: all .5s
	}
	.fade-enter,.fade-leave-to{
		opacity: 1
	}
</style>


