<template>
	<div class="cart-control">
		<transition name='move'>
			<div class="deleaseCart" v-show='foods.count>0' @click='deleaseCart'>
				-
			</div>
		</transition>
		<div class="count" >
			<span  v-show='foods.count>0'>{{foods.count}}</span>
			
		</div>
		<div class="addCart" @click='addCart'>
			+
		</div>
	</div>
</template>
<script type="text/javascript">

	export default{
		props:{
			foods:{
				type:Object
			}
		},
		methods:{
			addCart(event){
				if (!this.foods.count) {
					this.$set(this.foods,'count',1)
				}else{
					this.foods.count++
				}
				// console.log(this)
				this.$emit('addBall',event.target)
			},
			deleaseCart(event){
				if (this.foods.count) {
					this.foods.count--;
				}
			}
		}
	}
</script>
<style type="text/css" scoped>
	.cart-control{
		display: flex;
		flex-direction: row;
		font-size: 0
	}
	.addCart,.deleaseCart{
		width:20px;
		height: 20px;
		line-height: 20px;
		border-radius: 50%;
		text-align: center;
		background: #00a0dc;
		color: #fff;
		font-size: 16px	
	}
	.deleaseCart{
		font-size: 16px;
		background: #fff;
		color: #00a0dc;
		line-height: 14px;
		border: 2px solid #00a0dc;
		box-sizing: border-box;
	}
	.count{
		min-width: 12px;
		font-size: 12px;
		line-height: 20px;
		padding:0 10px
	}
	.move-enter-active{
		transition: all .4s ease
	}
	.move-leave-active{
		transition: all .4s ease
	}
	.move-enter,.move-leave-to{
		transform: translate3d(24px,0,0) rotate(180deg);
		opacity: 0

	}

</style>


