<template>
  <div class="mheader" @click="headerDetailShow">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">{{seller.description}}/{{seller.deliveryTime}}分钟送达</div>
        <div class="support" v-if="seller.supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="support-count">
        <span>{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right">></i>
      </div>
    </div>
    <div class="bulletin-wrapper">
      <span class="bulletin-title"></span>
      <span class="bulletin-text">{{seller.bulletin}}</span>
      <span></span>
    </div>
    <div class="background">
      <img :src="seller.avatar">
    </div>
    <div v-show="detailShow" class="header-detail">
      <div class="detail-wraper clearfix">
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <div class="star-wraper">
            <star :score='seller.score'></star>
          </div>
          <div class="bg-title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <ul v-if='seller.supports' class="supports">
            <li v-for='item in seller.supports'>
              <span class="icon-1" :class='classMap[item.type]'></span>
              <span class="text">{{item.description}}</span>
            </li>
          </ul>
          <div class="bg-title">
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
          <div class="bulletin">{{seller.bulletin}}</div>
        </div>
      </div>
      <div class="detail-close">
          <span @click.stop="headerDetailHide">X</span>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import Star from 'base/star/star'
import { getHeaderInfo } from "api/api";
export default {
  data() {
    return {
      seller: {},
      classMap: ["decrease", "discount", "guarantee", "invoice","decrease"],
      detailShow: false
    };
  },
  created() {
    this._getHeaderInfo();
  },
  methods: {
    _getHeaderInfo() {
      getHeaderInfo().then(res => {
        if (res.status == 200) {
          this.seller = res.data.seller;
        }
      });
    },
    headerDetailShow() {
      this.detailShow = true;
    },
    headerDetailHide(){
      this.detailShow = false
    }
  },
  components:{
    Star
  }
};
</script>
<style scoped>
.mheader {
  color: #fff;
  position: relative;
  background: rgba(7, 17, 27, 0.5);
  overflow: hidden;
}
.content-wrapper {
  display: flex;
  padding: 24px 12px 18px 24px;
  align-items: center;
  position: relative;
}
.content {
  margin-left: 16px;
}
.title {
  display: flex;
  margin: 2px 0 8px 0;
  align-items: center;
}
.brand {
  width: 30px;
  height: 18px;
  display: inline-block;
  background: url(brand@2x.png) no-repeat center center;
  background-size: 30px 18px;
}
.name {
  margin-left: 6px;
  font-size: 16px;
  font-weight: 700;
  color: rgba(255, 255, 255);
}
.description {
  font-size: 12px;
  font-weight: 200;
  margin-bottom: 8px;
}
.text {
  display: flex;
  font-size: 10px;
}
.support {
  display: flex;
  flex-direction: row;
}
.icon ,.icon-1{
  width: 12px;
  height: 12px;
  margin-right: 4px;
  display: inline-block;
}
.icon-1{
  width: 16px;
  height: 16px
}
.icon.decrease {
  background: url(decrease_2@2x.png) no-repeat center center;
  background-size: 12px 12px;
}
.icon.discount {
  background: url(discount_2@2x.png) no-repeat center center;
  background-size: 12px 12px;
}
.icon.guarantee {
  background: url(guarantee_2@2x.png) no-repeat center center;
  background-size: 12px 12px;
}
.icon.invoice {
  background: url(invoice_4@2x.png) no-repeat center center;
  background-size: 12px 12px;
}
.icon-1.decrease {
  background: url(decrease_2@2x.png) no-repeat center center;
  background-size: 16px 16px;
}
.icon-1.discount {
  background: url(discount_2@2x.png) no-repeat center center;
  background-size: 16px 16px;
}
.icon-1.guarantee {
  background: url(guarantee_2@2x.png) no-repeat center center;
  background-size: 16px 16px;
}
.icon-1.invoice {
  background: url(invoice_4@2x.png) no-repeat center center;
  background-size: 16px 16px;
}
.support-count {
  background-color: rgb(0, 0, 0, 0.2);
  font-size: 10px;
  font-weight: 200;
  padding: 0px 8px;
  position: absolute;
  right: 12px;
  bottom: 14px;
  height: 24px;
  line-height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 14px;
}
.icon-keyboard_arrow_right {
  line-height: 24px;
  margin-left: 2px;
  font-size: 10px;
}
.bulletin-wrapper {
  height: 28px;
  line-height: 28px;
  background: rgba(7, 17, 27, 0.2);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 8px;
}
.bulletin-title {
  flex: 0 0 22px;
  display: block;
  width: 22px;
  height: 12px;
  background: url(bulletin@2x.png) no-repeat center center;
  background-size: 22px 12px;
  margin-right: 4px;
}
.bulletin-text {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 10px;
}
.background {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  top: 0;
  left: 0;
  filter: blur(10px);
}
.background img {
  width: 100%;
  height: 100%;
}
.header-detail {
  transition: all 1s;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(7, 17, 27, 0.8);
  top: 0;
  left: 0;
  overflow: auto;
  color: #fff;
}
.detail-wraper {
  /* display: inline-block; */
  width: 100%;
  min-height: 100%;
}
.detail-main {
  padding-top: 64px;
  padding-bottom: 64px;
}
.detail-close {
  margin-top: -64px;
  display: flex;
  justify-content: center;
  font-size: 20px
}
.name{
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
  text-align:center;
}
.star-wraper{
  padding-top: 18px;
}
.bg-title{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin: 28px auto 24px;

}
.line{
  flex-grow: 1;
  border-bottom: 1px solid hsla(0,0%,100%,.2)
}
.bg-title .text{
  padding: 0 12px;
  font-weight: 700;
  font-size: 14px
}
.supports{
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.supports li{
  padding: 0 12px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
}
.supports li .text{
  font-size: 12px;
  line-height: 16px
}
.bulletin{
  width: 80%;
  margin:0 auto;
  font-size: 12px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
}
</style>


