<template>
	<div class="star" >
		<span v-for="(item,index) in itemClass" :class='item' class="star-item"></span>
	</div>
</template>
<script type="text/javascript">
	const LENGTH = 5;
	const CLS_ON = 'on';
	const CLS_OFF = 'off';
	export default{
		props:{
			score:{
				type:Number
			}
		},
		created(){
			console.log(this.score)
			
		},
		computed:{
			itemClass(){
				let result = [];
				const score = Math.floor(this.score);
				for (var i = 0; i < score; i++) {
					result.push(CLS_ON)
				}
				while(result.length<LENGTH){
					result.push(CLS_OFF)
				}
				return result
			}	
		}
	}
</script>
<style type="text/css" scoped>
.star{
  display: flex;
  flex-direction: row;;
  align-items: center;
  justify-content: center;
}
.star .star-item{
  width: 20px;
  height: 20px;
  background-size: 20px 20px;
  margin-right: 22px;
}
.star .star-item:last-child{
  margin-right: 0
}
.star .star-item.on{
  background: url(star24_on@2x.png) no-repeat center center;
}
.star .star-item.off{
  background: url(star24_off@2x.png) no-repeat center center;
}
</style>


