<template>
	<div class="goods">
		<div class="menu-wraper" ref='menuWraper'>
			<ul>
				<li v-for='(item,index) in goods' :key='item.name' class="mean-item" :class='currentIndex===index?"current":""' @click='selectMenu(index,$event)'>

					<div class="mean-item-child">
						<span v-show='item.type>0' :class='classMap[item.type]' class="icon"></span><span class="text">{{item.name}}{{currentIndex}}</span>
					</div>
					
				</li>
			</ul>
		</div>
		<div class="foods-wraper" ref='foodsWraper'>
			<ul>
				<li v-if='goods.length>0' v-for='item in goods' ref='foodsItem'>
					<h2 class="foods-title">{{item.name}}</h2>
					<ul>
						<li v-for='foods in item.foods' class="foods-item" >
							<div class="icon-img">
								<img width="57" height="57" :src="foods.image">
							</div>
							<div class="foods-text">
								<h2 class="name">{{foods.name}}</h2>
								<p class="foods-desc" v-if='foods.description'>{{foods.description}}</p>
								<div class="extra">
									<span class="count">月售{{foods.sellCount}}份</span>
									<span>好评率{{foods.rating}}%</span>
								</div>
								<div class="price">
									<span class="new">￥{{foods.price}}</span>
									<span class="old" v-if='foods.oldPrice'>￥{{foods.oldPrice}}</span>
								</div>
								<div class="cartcontrol-wrapper">
									<cart-control  @addBall='addFood' :foods='foods'></cart-control>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<shopcart :select-food = 'selectFoods'  :delivery-price='seller.deliveryPrice' :min-price='seller.minPrice' ref='shopcart'></shopcart>
	</div>
</template>
<script type="text/javascript">
	import {getHeaderInfo} from 'api/api'
	import BScroll from 'better-scroll'
	import shopcart from 'components/shopcart/shopcart'
	import cartControl from 'components/cartcontrol/cartcontrol'
	export default{
		data(){
			return {
				classMap: ["decrease", "discount", "guarantee", "invoice","decrease"],
				goods:[],
				seller:"",
				listHeight:[],
				scrollY:0
			}
		},
		created(){
			this.getGoodsList()
		},
		computed:{
			currentIndex(){
				for (var i = 0; i < this.listHeight.length; i++) {
					let height1 = this.listHeight[i];
					let height2 = this.listHeight[i+1];
					if (!height2||(this.scrollY>=height1&&this.scrollY<height2)) {
						return i
					}
				}
				return 0
			},
			selectFoods(){
				let foods = [];
				this.goods.forEach((food)=>{
					food.foods.forEach((food)=>{
						if (food.count) {
							foods.push(food)
						}
					})
				})
				return foods
			}
		},
		methods:{
			getGoodsList(){
				getHeaderInfo().then(res=>{
					console.log(res.data.goods)
					this.goods = res.data.goods;
					this.seller = res.data.seller;
					this.$nextTick(()=>{
						this._initScroll();
						this._calculHeight()
					})
				})
			},
			_initScroll(){
				this.meanScroll = new BScroll(this.$refs.menuWraper,{
					click:true
				})
				this.foodScroll = new BScroll(this.$refs.foodsWraper,{
					click:true,
					probeType:3
				});
				this.foodScroll.on('scroll',(pos)=>{
					let posy = Math.abs(Math.round(pos.y))
					// console.log(posy)
					this.scrollY = posy
				})
			},
			_calculHeight(){
				let foodList = this.$refs.foodsItem;
				let height = 0;
				this.listHeight.push(height);
				for (let i = 0; i < foodList.length; i++) {
					let item = foodList[i];
					height+=item.clientHeight;
					this.listHeight.push(height)
				}
				console.log(this.listHeight)
			},
			selectMenu(index,event){
				if (!event._constructed) {
					return
				}
				let foodList = this.$refs.foodsItem;
				let el = foodList[index];
				this.foodScroll.scrollToElement(el,300)
				console.log(index)
			},
			addFood(target){
				this.$refs.shopcart.drop(target)
			}
		},
		components:{
			shopcart,
			cartControl
		}
	}
</script>
<style type="text/css" scoped>
	.goods{
		position: absolute;
		width: 100%;
		top: 174px;
		bottom: 48px;
		display: flex;
		overflow: hidden;
	}
	.menu-wraper{
		flex: 0 0 80px;
		width: 80px;
		background: #f3f5f7
	}
	.foods-wraper{
		flex: 1
	}
	.mean-item{
		padding: 0 10px;
		margin: 0 auto;
	}
	
	.icon{
		width: 12px;
		height: 12px;
		display: inline-block;
		margin-right: 4px;
		/*flex:0 0 12px*/
		vertical-align: top;
	}
	.icon.decrease {
	  background: url(decrease_3@2x.png) no-repeat center center;
	  background-size: 12px 12px;
	}
	.icon.discount {
	  background: url(discount_3@2x.png) no-repeat center center;
	  background-size: 12px 12px;
	}
	.icon.guarantee {
	  background: url(guarantee_2@2x.png) no-repeat center center;
	  background-size: 12px 12px;
	}
	.icon.invoice {
	  background: url(invoice_4@2x.png) no-repeat center center;
	  background-size: 12px 12px;
	}
	.mean-item{
		display: flex;
		height: 56px;
		line-height: 14px;
		justify-content: center;
		align-items: center;
	}
	.mean-item .text{
		font-size: 12px
	}
	.foods-title{
		height: 26px;
		color: #666;
		border-left: 2px solid #d9dde1;
		line-height: 26px;
		padding-left: 14px;
		font-size: 12px;
		background: #f3f5f7
	}
	.foods-item{
		display: flex;
		margin:18px 18px 0 18px;
		padding-bottom: 18px;
		box-sizing: border-box;
		position: relative;
	}
	.icon-img{
		flex: 0 0 57px;
		width: 57px;
		margin-right: 10px
	}
	.foods-text{
		flex: 1
	}
	.foods-text .name{
		color: #333;
		font-size: 14px;
		line-height: 14px;
		margin: 2px 0 8px
	}
	.foods-text .foods-desc{
		line-height: 12px;
		font-size: 10px;
		padding-bottom: 6px;
		color: #999
	}
	.extra{
		line-height: 12px;
		font-size: 10px;
		color: #999
	}
	.extra .count{
		margin-right: 12px;
		
	}
	.price{
		line-height: 24px;
		position: relative;
	}
	.price .new{
		color: #f01414;
   		font-size: 14px;
    	margin-right: 8px
	}
	.price .old{
		color: #999;
    	font-size: 10px;
    	text-decoration: line-through
	}
	.current{
		background: #fff;
		font-weight: 700
	}
	.cartcontrol-wrapper{
		position: absolute;
		right: 0;
		bottom: 18px
	}
</style>


