<template>
	<div class="shopcart-detail">
		<div class="shop-title">
			<div class="title">购物车</div>
			<div class="empty" @click='empty'>清空</div>
		</div>
		<div class="list-content" ref='listContent'>
			<ul>
				<li v-for='item in selectFood' class="list-item">
					<div class="name">{{item.name}}</div>
					<div class="price">￥{{item.price*item.count}}</div>
					<div class="cartcontrol-content">
						<cart-control :foods='item'></cart-control>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>
<script type="text/javascript">
	import cartControl from 'components/cartcontrol/cartcontrol'
	import BScroll from 'better-scroll'
	export default{
		props:{
			selectFood:{
				type:Array,
				default(){
					return []
				}
			}
		},
		methods:{
			empty(){
				this.selectFood.forEach((food)=>{
					food.count = 0
				})
			},
			refresh(){
				if (!this.scroll) {
					this.scroll = new BScroll(this.$refs.listContent,{
						click:true
					})
				}else{
					this.scroll.refresh()
				}
			}
		},
		components:{
			cartControl
		}
	}
</script>
<style type="text/css" scoped>
	.shop-title{
		height: 40px;
		line-height: 40px;
		background: #f3f5f7;
		padding: 0 18px
	}
	.title{
		font-size: 14px;
		float: left;
		color: #333;
	}
	.empty{
		float: right;;
		color: #00a0dc;
		font-size: 12px
	}
	.list-content{
		max-height: 217px;
		overflow: hidden;
		background: #fff;
		padding: 0 18px
	}
	.list-item{
		display: flex;
		padding: 12px 0;
		position: relative;
		align-items: center;
	}
	.name{
		font-size: 14px;
		color: #333;
	}
	.price{
		font-size: 14px;
		color: #f01414;
		font-weight: 700;
		position: absolute;
		right: 90px;
	}
	.cartcontrol-content{
		position: absolute;
		right: 0;
	}
</style>


