import Vue from 'vue'
import Router from 'vue-router'
import Seller from 'components/seller/seller'
import Goods from 'components/goods/goods'
import Ratings from 'components/ratings/ratings'
import Errors from 'base/error/error'

Vue.use(Router)

 const router = new Router({
  routes:[{
  	path:'/',
  	redirect:'/goods',

  },{
  	path:'/goods',
  	component:Goods,
  	meta:{
  		title:'我是goods'
  	}
  },{
  	path:'/seller',
  	component:Seller,
  	meta:{
  		title:"我是seller"
  	}
  },{
  	path:'/ratings',
  	component:Ratings,
  	meta:{
  		title:"我是rating"
  	}
  },{
  	path:'*',
  	component:Errors
  }]
})
router.beforeEach((to, from, next) => {
 /* 路由发生变化修改页面title */
	if (to.meta.title) {
		document.title = to.meta.title
	}
	 next()
})
export default router



